import { Injectable,EventEmitter } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Usuario } from './usuario';
import { Router } from '@angular/router';
import { take } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private readonly API = "https://monitoramento-backend.herokuapp.com/users";

  private usuarioAutenticado: boolean = false;
  mostrarMenuEmitter = new EventEmitter<boolean>();

  constructor(private router: Router, private http: HttpClient) { }

  list(){
    return this.http.get<Usuario[]>(this.API).pipe(
    );
  }

  private authUser(usuario: Usuario){
    console.log(usuario);
    this.http.post(this.API, usuario)
        .pipe()
        .subscribe((data: Usuario) => {

          console.log(data);

            if(data !== null){

                this.usuarioAutenticado = true;

                sessionStorage.setItem("usuarioAutenticado", String(this.usuarioAutenticado));

               this.mostrarMenuEmitter.emit(this.usuarioAutenticado);

               this.router.navigate(['/']);
            }
            else{
             this.usuarioAutenticado = false;
             this.mostrarMenuEmitter.emit(this.usuarioAutenticado);
            }  

        });
  }

  fazerLogin(usuario: Usuario){

    this.authUser(usuario);

  }

  usuarioEstaAutenticado(){
    if (!this.usuarioAutenticado){
      return Boolean(sessionStorage.getItem("usuarioAutenticado"));
    }
    return this.usuarioAutenticado;
  }

}
